@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&family=Roboto:wght@100;300;400;700&display=swap');
@import "normalize.css";


@mixin flex($parameter1,$parameter2,$parameter3,$parameter4) {
  display: $parameter1;
  flex-direction: $parameter2;
  justify-content: $parameter3;
  align-items: $parameter4;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  background-color: #ffff;
  font-family: 'Roboto', sans-serif;
}

.container {
  margin: 0px 102.4px;

  &__align {
    @include flex(flex, row, space-between, center);
    height: 48.6px;

    &--footer {
      justify-content: center;
    }
  }

  &--left {
    padding: 0px 15px;
    @include flex(flex, row, space-between, center);
    justify-content: flex-start;
  }

  &--right {
    @include flex(flex, row, space-between, center);
    justify-content: flex-end;
    padding: 0px 15px;

  }

  div {
    text-align: center;
  }

  span {
    display: inline-block;
    padding: 0px 15px 0px 0px;
    height: 28.6px;
    text-align: center;
    font-size: 11px;

  }
}

nav.navigation {
  &__bar {
    height: 90.4px;
    @include flex(flex, row, space-between, center);

    a {
      text-decoration: none;
      padding: 40px 15px;
      height: 90.4px;
      font-weight: bold;
      color: black;
      font-size: 13px;
    }
  }

  &__link {
    text-align: center;
    font-size: 11px;
  }

  @include flex(flex, row, space-between, center);
}

.logo {
  padding: 0px 15px 0px 0px;
  width: 88px;
  height: 26px;
}

.header__top {
  background-color: #fef7f6;
  height: 48.6px;
  font-size: 11px;
  width: 100%;
}


.align-left {
  justify-content: flex-start;
}

.centered {
  justify-content: center;
}

.align-right {
  justify-content: flex-end;

  :hover {
    color: #ff5353;
  }
}

.banner {

  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  background-image: url("https://demo.themeies.com/silon/images/bg-1.jpg");


  &__fluid {
    position: relative;
    margin-right: auto;
    margin-left: auto;
    display: flex;
  }

  &__column1 {
    width: 50%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    &--content {
      width: 40%;
      position: absolute;
      left: 50%;
      top: 50%;
    transform: translate(-50%, -50%);
      @include flex(flex, column, space-around, center);
    }
  }
  &__column2 {
    position: relative;
    display: flex;
    justify-content: flex-end;
    width: 50%;
  }
}

  .content {
    position: relative;
    text-align: center;
    padding: 15px 15px;
    @include flex(flex, column, space-between, center);
    flex-wrap: nowrap;

    &__button {
      background: #ff5353;
      width: 170px;
      height: 51.2px;
      border-radius: 1em;

      &--content {
        display: inline-block;
        text-transform: uppercase;
        color: #ffff;
        padding: 20px 25px;
        text-align: center;
        text-decoration: none;
        margin-left: auto;
        margin-right: auto;
        font-size: 14px;
        font-weight: 500;
      }
    }
  }
.banner__colored--image {
  height: 80%;
}

p.tagline {
  height: 11.2px;
  text-transform: uppercase;
  color: #565656;
}

a {
  display: inline-block;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

h1 {
  font-size: 50px;
  text-align: center;
}

.featuredProduct {
  &__container {
    padding: 0px 50px;
    @include flex(flex, column, space-between, center);
    display: block;
    flex-wrap: wrap;
  }

  &__title {
    height: 123.188px;
  }
}

h2 {
  font-size: 36px;
