}

.cloud {
  position: absolute;
  height: 12px;
  width: 12px;
  border-radius: 50%;
  background-color: #ff5353;
  top: 34%;
  right: 21%;
  color: #fef7f6;
  font-size: 9px;
}

div.cloud p {
  transform: translate(5%, -80%)
}

