}

.columns {
  @include flex(flex, row, space-around, center);
  position: relative;
  margin-bottom: 50px;

}
.product:hover {
 .product__item--hover{
   visibility: visible;
 }
  position: relative;

}
.product {
  &__item {
    &--hover {
position: absolute;
      visibility: hidden;
      display: flex;
      text-decoration: none;
      top: 60%;
      left: 50%;
      margin-right: -50%;
      transform: translate(-50%, -50%);
      transition: 1.6s ease-in-out;
    }
    &--details {


      p.item__name {
        font-size: 14px;
        color: #565656;
      }
    }
  }
}
